
import { mapState,mapGetters } from "vuex";
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
props: ["visitabal","videoid"],
directives: {
TransferDom
},
components: {
Popup,
XTextarea,
Group,
XButton
},
computed: {
...mapState({
config: state => state.config
}),
...mapGetters({
user:"getUser"
})
},
watch: {
visitabal(val){
if(val)
{
this.$refs.comentArea.$el.getElementsByTagName("textarea")[0].focus();
}
}
},
methods: {

submitComent() {
let that = this;
if (!this.comentValue){

this.$vux.alert.show({
title: "",
content: "<span style='color:red'></span>",
buttonText:"",
onHide:function(){
that.isSubmit = false;
}
});
}
if (this.isSubmit)
{
return;
}
this.isSubmit = true;
let content = this.comentValue;
let url = this.config.url.base + this.config.url.subComent;
let params = {
api:url,
body:{
vid:this.videoid,
content:content,
accessToken:this.user.accessToken
},
cb:this.cbSubComent
}
this.$store.dispatch("subComment",params);
},
cbSubComent(data){
if(data.success)
{
this.$emit("comentSuccess",data.obj.item);
this.comentValue = "";
this._closeComent();
this.isSubmit = false;
this.$vux.toast.text('', 'bottom');
}
else
{
this.isSubmit = false;

this.$vux.alert.show({
title: "",
content: "<span style='color:red'>"+data.obj.errorMsg+"</span>",
buttonText:""
});
}
},

_closeComent() {
this.$emit("closeModel");
}
},
data(){
return {
comentValue: "",
isSubmit: false
}
}
}



