
import Vue from "vue";
import {mapGetters,mapState} from 'vuex';
import XProgress from 'vux/src/components/x-progress/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

import cameraActionsheet from "../../common/cameraActionsheet";
import videoPlayer from "../../common/videoPlay";
export default {
directives: {
TransferDom
},
created () {
let that = this;
this.cordova.on("deviceready", () => {
that.media = new Media(this.audioName,this.recordSuccess,this.recordError);
})
},
mounted () {



},
destroyed(){
this.$store.commit("SET_HEADER_RIGHT", {
option:{showMore: false},
rightContent:"",
action:function(){
return ;
}
});
},
data() {
return {
cordova:Vue.cordova,
publishModel:false,
isPublish:false,
videoTitle:"",

audioName:"videoRecord.wav",
audioData:"",
isUploadAudio:false,
isMerge:false,
loadingTips:"",
timestamp:"",
media:null,
recordPreload:3,
recordText:"",
disableRecord:true,
prepareRecord:false,
isRecording:false,
recorded:false,
recordPrecent: 0,
isPreview:false,

isShow: false,
videoUri:"",
isUpload:false,
precent: 0,
menus: {
video: "",
takeVideo: "10s"
},
videoSrc:"",
oldVideoSrc:"",
video_public_id:"",
qiniu_key:"",
videoOptions:{
playStatus:"autoplay",
muteStatus:true,
height:"280",
width:"100%",
poster:""
},
isChangeVideo:false,
rightOption:{
option:{showMore: false},
rightContent:"",
action:this.changeVideo
}
};
},
computed: {
...mapGetters({
user:"getUser"
}),
...mapState({
config: state => state.config
})
},
methods: {
dopublishModel(){
this.publishModel = true;
},
publish(){
if(this.isPublish)
return ;
this.isPublish = true;
let title = this.videoTitle;
if(!title)
{
this.isPublish = false;
this.$vux.toast.show({
text:"",
type:"warn"
});
return ;
}
let body = {
title: title,
qiniu_key: this.qiniu_key,
accessToken:this.user.accessToken
}
let creationUrl = this.config.url.base + this.config.url.creation;
let params = {
api:creationUrl,
body:body,
cb:this.cbCreation
};
this.$store.dispatch("creation",params);
},
cbCreation(data){
this.isPublish = false;
if(data.success)
{
let that = this;
this.videoTitle = "";
this.initData();
this._closePublishModel();
this.$vux.toast.show({
text:"",
type:"success",
onHide:()=>{
that.$router.replace({ name: "index" });
}
});
}
else
{
this.$vux.toast.show({
text:data.obj.errorMsg,
type:"warn"
});
}
},
_closePublishModel(){
this.publishModel = false;
},
PlayPreview(){
this.isPreview = true;
let that = this;
that.$nextTick(()=>{
let video = that.$refs.videoPlay;
console.log(video.video.duration);
video.reloadvideo();
video.video.autoplay=true;
video.playVideo();
})
},
readAudioFile(isUpload){
let that = this;
resolveLocalFileSystemURL(cordova.file.externalRootDirectory, function(dirEntry) {
dirEntry.getFile(that.audioName,{},function(fileEntry){
fileEntry.file(function(file){

let Reader = new FileReader();
Reader.onloadend = function(){
that.audioData = this.result;
if(isUpload)
that.uploadAudio();
}
Reader.readAsDataURL(file);
})
})
})
},
uploadAudio(){

let urlConfig = this.config.url;
let signatureUrl = urlConfig.base + urlConfig.imageSignature;
this.timestamp = Date.now();

let body = {
accessToken: this.user.accessToken,
timestamp:this.timestamp,
type: "audio",
cloud:"cloudinary"
};
let param = {
api: signatureUrl,
body: body,
cb: this.cbAudioSignature
};
this.$store.dispatch("getSignature", param);
},
cbAudioSignature(data){
if (data.success) {
let signature = data.obj.signature;
let body = new FormData();
let audioURL = "";

let folder = data.obj.folder;
let tags = data.obj.tags;
let key = data.obj.key;
body.append("folder", folder);
body.append("signature", signature);
body.append("timestamp", this.timestamp);
body.append("tags", tags);
body.append("public_id", key);
body.append("api_key", this.config.cloudinary.api_key);
body.append("resource_type", "video");
body.append("file", this.audioData);
audioURL = this.config.cloudinary.apiBase + this.config.cloudinary.audio;
let param = {
api: audioURL,
body: body,
header: {
"Content-Type": "multipart/form-data"
},
cb: this.cbuploadAudeo,
uploading: this.onAudioUploading
};
this.$store.dispatch("uploadFile", param);
}
},
cbuploadAudeo(data){
if(data.public_id)
{
this.isUploadAudio = false;
this.isMerge = true;
this.loadingTips = "...";

if(!this.video_public_id)
{
this.recorded = false;
this.$vux.toast.show({
text:"",
type:"warn"
})
}
let audio = {
public_id:data.public_id,
video_public_id:this.video_public_id,
qiniu_key:this.qiniu_key
};
let MergeVideoURL = this.config.url.base + this.config.url.MergeVideo;
let param = {
api: MergeVideoURL,
body: {
accessToken: this.user.accessToken,
audio:JSON.stringify(audio)
},
cb: this.cbMergeVideo,
};
try {
this.$store.dispatch("MergeVideo", param);
} catch (error) {
console.log(error);
this.recorded = false;
this.$vux.toast.show({
text:"",
type:"warn"
})
}
}
else{
this.recorded = false;
this.$vux.toast.show({
text:"",
type:"warn"
})
}

},
onAudioUploading(progressEvent){
return ;
},
cbMergeVideo(data){
this.isMerge = false;
this.loadingTips = "";
if(data.success){
this.videoSrc = this.config.qiniu.fileUrl + "/" +data.obj.video_key;
this.videoOptions = {
playStatus:"false",
muteStatus:false,
height:"280",
width:"100%",
poster: this.config.qiniu.fileUrl + "/" +data.obj.poster_key
}
this.$nextTick(()=>{
this.$refs.videoPlay.reloadvideo();
});
this.isPreview = true;
}
else{
this.recorded = false;
this.$vux.toast.show({
text:data.obj.errorMsg,
type:"warn"
})
}
},
doPrepareRecord(){
if(this.prepareRecord)
return;
if(this.disableRecord)
return;
if(this.isPreview)
{
this.disableRecord = true;
this.videoSrc = this.oldVideoSrc;
this.videoOptions = {
playStatus:"autoplay",
muteStatus:true,
height:"280",
width:"100%",
poster:""
};
return ;
}
this.recorded = false;
this.prepareRecord = true;
},

preRecord(){
this.recordText = "GO";
this.recordPreload = 3;
let that = this;
setTimeout(() => {
that.recordText = "";
that.prepareRecord = false;
that.isRecording = true;
that.recordPrecent = 0;
that.disableRecord = true;
that.isPreview = false;
that.$nextTick(()=>{
let videoPlay = that.$refs.videoPlay;
videoPlay.playVideo();

this.media.startRecord();
})
}, 1000);
},
endRecord(){
if(this.isPreview)
{

this.prepareRecord = true;
this.recorded = false;
}
if(this.disableRecord)
{

this.disableRecord = false;
}
if(this.isRecording){
this.isRecording = false;
this.recordPrecent = 0;
this.recorded = true;
this.media.stopRecord();
console.log("videoEnd");
}
},
onPause(){
console.log("onPause");
},
onPlay(){
console.log("play");
},
onWaiting(){
console.log("onWaiting");
if(this.isRecording)
this.media.pauseRecord();
},
onPlaying(){
console.log("onPlaying");
if(this.isRecording){
this.media.resumeRecord();
}
},
onVideoProcess(data){
if(this.isRecording){
this.recordPrecent = data.precent;
}
},
recordSuccess(){
if(this.recorded)
{
this.isUploadAudio = true;
this.loadingTips="...";
this.readAudioFile(true);
}
},
recordError(err){
console.log("Error recording"+err);
},
initData(){
this.audioData = "";
this.recorded = false;
this.recordPrecent =  0;
this.isPreview = false;
this.disableRecord = true;
this.prepareRecord = false;
this.isRecording = false;
this.isPublish = false;
this.isMerge = false;
this.isUploadAudio = false;
this.videoSrc = "";
this.video_public_id= "";
this.qiniu_key= "";
this.videoOptions = {
playStatus:"autoplay",
muteStatus:true,
height:"280",
width:"100%",
poster:""
};
},
changeVideo(){
this.isChangeVideo = true;
this.showCamera();
},
showCamera() {
this.isShow = true;
},
cameraSuccess(files) {
if(this.isChangeVideo)
this.initData();
let file = "";
if(files[0].fullPath)
{
file = files[0].fullPath;
}
else
{
file = "file://"+files;
}
this.videoUri = file;

this.recorded = false;

this.uploadVideo();
},
cameraError(message) {

console.log(message);
},

uploadVideo() {
this.isUpload = true;

let urlConfig = this.config.url;
let signatureUrl = urlConfig.base + urlConfig.imageSignature;

let body = {
accessToken: this.user.accessToken,
type: "video",
cloud:"qiniu"
};
let param = {
api: signatureUrl,
body: body,
cb: this.cbSignature
};
this.$store.dispatch("getSignature", param);
},
cbSignature(data){
if (data.success) {
let signature = data.obj.signature;
let imageURL = this.config.qiniu.upload;
let key = data.obj.key;


let options = new FileUploadOptions();
options.fileKey = "file";
options.fileName = key;
options.mimeType = "video/mp4";
var params = {};
params.key = key;
params.token = signature;
options.params = params;
let ft = new FileTransfer();
ft.onprogress = this.onUploading;
ft.upload(this.videoUri, encodeURI(imageURL), this.cbuploadVideo, this.cbuploadVideoFail, options);
}
else
{
this.$vux.toast.show({
text:data.obj.errorMsg,
type:"warn"
})
}
},
onUploading(progressEvent){
if (progressEvent.lengthComputable) {
let that = this;
let loaded = progressEvent.loaded,
total = progressEvent.total;
that.$nextTick(() => {
that.precent = Math.round(loaded / total * 100);
});
}
},
cbuploadVideo(data){
let dataString = data.response;
data = JSON.parse(dataString);
this.videoSrc = this.config.qiniu.fileUrl + "/" +data.key;
this.oldVideoSrc = this.config.qiniu.fileUrl + "/" +data.key;
this.isUpload = false;
this.isChangeVideo = false;
this.precent = 0;
this.$store.commit("SET_HEADER_RIGHT", this.rightOption);
let params = {
api:this.config.url.base + this.config.url.video,
body:{
accessToken: this.user.accessToken,
video: dataString,
videoSrc: this.videoSrc
},
cb:this.cbSaveVideoinfo
};
this.$store.dispatch("saveVideoInfo",params);
},
cbuploadVideoFail(error){
console.log("upload error source " + error.source);
console.log("upload error target " + error.target);
},
cbSaveVideoinfo(data){
if(data.success){
let fileSrc = data.obj.video.src;
this.qiniu_key = data.obj.video.qiniu_key;
let params = {
api:this.config.url.base + this.config.url.saveCloudinaryInfo,
body:{
accessToken: this.user.accessToken,
qiniu_key: this.qiniu_key,
src:fileSrc
},
cb:(data)=>{
this.video_public_id = data.obj.video.public_id;
}
};
this.$store.dispatch("addCloudinaryVideoInfo",params);
}
}
},
components: {
cameraActionsheet,
XProgress,
videoPlayer,
Countdown,
XButton,
Popup,
XTextarea,
Group
}
};



