
import LoadMore from 'vux/src/components/load-more/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import videoRow from "./videoRow"
import { mapState,mapGetters } from "vuex";
export default {
created() {
this._getVideoLists();
},
mounted() {
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top : 0});
this.$refs.scrollerBottom.donePulldown();
this.onFetching = false;
});
},
methods: {
_getVideoLists() {

this.onFetching = true;

this.$store.dispatch("getVideolists");
},

onScrollBottom() {
if(!this.videoMore)
{
this.$refs.scrollerBottom.reset();
this.$refs.scrollerBottom.donePulldown();
}
if (this.onFetching) return;
this._getVideoLists();
this.$nextTick(() => {
this.$refs.scrollerBottom.reset();
this.$refs.scrollerBottom.donePullup();
this.onFetching = false;
});
},

refreshData() {
if (this.onFetching) {
return;
} else {

this.$store.dispatch("getVideolistsFresh");
this.$nextTick(() => {
this.$refs.scrollerBottom.reset({top : 0});
this.$refs.scrollerBottom.donePulldown();
this.onFetching = false;
});
}
}
},
data() {
return {
onFetching: false,
pulldownConfig: {
content: "",
downContent: "",
upContent: "",
loadingContent: "..."
},
pullupConfig: {
upContent: " ",
downContent: " ",
loadingContent: " "
}
};
},
computed: {
...mapState({
config: state => state.config
}),
...mapGetters({
videoLists:"getVideoList",
videoMore:"getVideoMore"
})
},
components: {
videoRow,
Scroller,
LoadMore
}
};



