
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { mapGetters } from "vuex";
export default {
created () {
this._getMyvideoLists();
let backurl = {name:'user'};
this.$store.dispatch("setBackurl",backurl);
},
destroyed () {
this.$store.dispatch("setBackurl","");
},
data(){
return {
};
},
components: {
Flexbox,
FlexboxItem,
Scroller,
LoadMore
},
methods: {

goDetail(row){
this.$router.push({name:'videoDetail',params:{video:row}});
},
_getMyvideoLists(){

this.onFetching = true;

this.$store.dispatch("getMyvideoLists",this.user.id);
},

onScrollBottom() {
if(!this.hasMore)
{
this.$refs.scrollerBottom.reset();
this.onFetching = false;
}
if (this.onFetching) {
return;
} else {
this._getMyvideoLists();
this.$nextTick(() => {
this.$refs.scrollerBottom.reset();
this.onFetching = false;
});
}
}
},
computed: {
...mapGetters({
videoLists:"getMyvideoList",
hasMore:"getMyvideoMore",
user:"getUser"
})
}

}



