
import Card from 'vux/src/components/card/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { mapState } from "vuex";
import comentModel from "./Comentmodel";
export default {
props: ["item"],
methods: {

like() {
let isLike = 0 == this.item.isLike ? 1 : 0;
this.$store.dispatch("dolike",{isLike:isLike,videoid:this.item.id,cb:this.cblike});
},
cblike(data){
if(data.success)
{
this.item.isLike = data.obj.isLike;
}
else{
this.$vux.toast.show({
text:data.obj.errorMsg,
type:"warn"
})
}
},

goDetail(row){
this.$router.push({name:'videoDetail',params:{video:row}});
},

comment(row){
this.videoId = row.id;
this.comentVisitable = true;
},
_closeComent(){
this.comentVisitable = false;
}
},
components: {
Card,
Flexbox,
FlexboxItem,
comentModel
},
data(){
return {
comentVisitable:false,
videoId:0,
}
},
computed: {
...mapState({
config: state => state.config,
route: state => state.route
})
}
};



