export default {
  props: ["src","options"],
  mounted () {
    this.screenW = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
    this.video = this.$refs.myvideo;
    let that = this;
    this.video.addEventListener('canplaythrough',function(){
        if(that.options.playStatus && "autoplay"==that.options.playStatus){
            that.playVideo();
        }
    });
    this.video.addEventListener('canplay', ()=>{
         this.isLoaded = true;
      })
    this.video.addEventListener('playing', ()=>{
      if(!this.duration){
          this.duration = this.video.duration;
        }
      this.$emit("playing");
    })
    this.video.addEventListener('pause', ()=>{
      this.$emit("pause");
    })
    this.video.addEventListener('waiting', ()=>{
      this.$emit("waiting");
    })
    
    this.video.addEventListener('play', ()=>{
      this.$emit("play");
    })
  },
  data(){
    return {
      video:null,
      screenW: 0,
      isPlay:false,
      isPause:false,
      precent: 0,
      duration: 0,
      isLoaded:false
    }
  },
  methods: {
    reloadvideo(){
      this.video.load();
    },
      //
    playVideo(){
      this.isPlay = true;
      this.isPause = false;
      this.video.play();
      if(!this.isPlay)
      {
        this.precent = 0;
        this.video.currentTime = 0;
      }
      this.video.addEventListener('durationchange', ()=>{
        console.log(this.video.duration+"durationchange");      
        this.duration = this.video.duration;
      })
      
      this.video.addEventListener('timeupdate', this.timeline)
      this.video.addEventListener('ended', (e) => {
            this.isPlay = false;
            this.isPause = false;
            this.precent = 100;
            this.$emit("videoEnd");
        })
    },
    pauseVideo(){
      this.isPause = true;
      this.video.pause();
    },
    timeline () {
        if(this.duration)
        {
          this.precent = Math.floor((this.video.currentTime / this.duration)*100)
          this.$emit("process",{precent:this.precent,currentTime:this.video.currentTime});
        }
    },
  }
}



